// pages / dashboard / payment

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class Payment extends React.Component {
    componentDidMount() {
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
              <span>

                  {/* Start of Helmet */}
                  <Helmet title="Payment" meta={[
                      {
                          name: 'description',
                          content: 'Payment'
                      }, {
                          name: 'keywords',
                          content: 'Payment'
                      }
                  ]}/>
                  {/* End of Helmet */}


                  <div className="row wrapper border-bottom white-bg page-heading">
                      <div className="col-lg-9">
                          <h2>Payment</h2>
                          <ol className="breadcrumb">
                              <li>
                                  <Link to="/dashboard">Home</Link>
                              </li>
                              <li className="active">
                                  <strong>Payment Available</strong>
                              </li>
                          </ol>
                      </div>
                  </div>

                  <div className="wrapper wrapper-content animated fadeInRight">
                      <div className="row">
                          <div className="col-lg-4">
                              <div className="contact-box">
                                  <a href="#">
                                      <div className="col-sm-4">
                                          <div className="text-center">
                                              <i className="fa fa-lightbulb-o" style={{fontSize: '100px', lineHeight: '115px'}}></i>
                                          </div>
                                      </div>
                                      <div className="col-sm-8">
                                          <h3><strong>Electric Bill</strong></h3>
                                          <p><i className="fa fa-book"></i> Next Payment Due: 12/9/17</p>
                                          <address>
                                              <strong>Account Number:</strong> 1001025561656<br/>
                                              <strong>Last Payment Amount:</strong> RM 180.00<br/>
                                              <strong>Current Outstanding:</strong> RM 100.00
                                          </address>
                                      </div>
                                      <div className="clearfix"></div>
                                  </a>
                              </div>
                          </div>
                          <div className="col-lg-4">
                              <div className="contact-box">
                                  <a href="#">
                                      <div className="col-sm-4">
                                          <div className="text-center">
                                              <i className="fa fa-bath" style={{fontSize: '100px', lineHeight: '115px'}}></i>
                                          </div>
                                      </div>
                                      <div className="col-sm-8">
                                          <h3><strong>Water Bill</strong></h3>
                                          <p><i className="fa fa-book"></i> Next Payment Due: 12/9/17</p>
                                          <address>
                                              <strong>Account Number:</strong> 1001025561656<br/>
                                              <strong>Last Payment Amount:</strong> RM 180.00<br/>
                                              <strong>Current Outstanding:</strong> RM 100.00
                                          </address>
                                      </div>
                                      <div className="clearfix"></div>
                                  </a>
                              </div>
                          </div>
                          <div className="col-lg-4">
                              <div className="contact-box">
                                  <a href="#">
                                      <div className="col-sm-4">
                                          <div className="text-center">
                                              <i className="fa fa-wrench" style={{fontSize: '100px', lineHeight: '115px'}}></i>
                                          </div>
                                      </div>
                                      <div className="col-sm-8">
                                          <h3><strong>Maintenance Bill</strong></h3>
                                          <p><i className="fa fa-book"></i> Next Payment Due: 12/9/17</p>
                                          <address>
                                              <strong>Account Number:</strong> 1001025561656<br/>
                                              <strong>Last Payment Amount:</strong> RM 180.00<br/>
                                              <strong>Current Outstanding:</strong> RM 100.00
                                          </address>
                                      </div>
                                      <div className="clearfix"></div>
                                  </a>
                              </div>
                          </div>
                      </div>
                  </div>
              </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(Payment);



